
.TodoItem{
    color: var(--bs-darker-lt);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    padding: 15px;
    box-shadow: 0 0.5px 1px var(--bs-lightest-lt);
    &__title{
    flex: 1;
    font-size: 16px;
    font-weight: 600;
    &.item-done {
        text-decoration: line-through;
        text-decoration-color: black;
        text-decoration-thickness: 1px;
        color: var(--bs-lightest-lt) ;
    }
    }
    & .checkbox {
        display: block;
        margin-bottom: 22px;
    }

    & button img{
        width: 70%;
        height: 70%;

        &:hover{
            //this changes the image color to red on hover neat
            filter: sepia(1) saturate(10000%) hue-rotate(-30deg);
        }
    }

    &.dark{
        box-shadow: 0 0.5px 1px var(--bs-darker-dt);
        color: var(--bs-lightest-lt);
        & .item-done{
            color: var(--bs-darker-lt);
        }
    } 
}
.todoListFooter{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 40px;
    & button{
        color: var(--bs-dark-lt);
    }
}

